
:root{
  --negro: #10002b;
  --gris: #10002b;
  --morado: #81007F;
}
html{
  font-size: 62.5%;
  box-sizing: border-box;
}
*,*:before, *:after{
  box-sizing: inherit;
}
body{
  font-size: 1.6rem;
  line-height: 1.5;
  font-family: 'PT Sans', sans-serif;
}
h1, h2, h3{
  margin: 0 0 2rem 0;
  line-height: 1.5;
}
h1, h2, h6 {
  font-family: 'Roboto Slab', serif;
  font-weight: 700;
}
h3 {
  font-family: 'PT Sans', sans-serif;
}
ul{
  list-style: none;
  margin: 0;
  padding: 0;
}
a{
  text-decoration: none;
}
.listado-productos {
  background-color: #f3f3f3;
}
.contenedor {
  max-width: 1200px;
  width: 95%;
  padding: 5rem 0;
  margin: 0 auto;
}
.bg-white {
  background-color: #FFF;
}